import { NoteFiltered } from "./NoteFiltered";

export const Filter = ({ handleFilter, handleOnChangeFilter, filterby, filtered }) => {
    return (
      <div>
        <form onSubmit={handleFilter}>
          filter shown with:{" "}
          <input type="text" onChange={handleOnChangeFilter} value={filterby} />
        </form>
        {filterby.length <= 0
          ? " "
          : filtered.map((filter) => (
              <NoteFiltered key={filter.title} person={filter} />
            ))}
      </div>
    );
  };