import "./App.css";
import React, { useState, useEffect } from "react";
import { Filter } from "./Filter/Filter";
import { Notes } from "./Notes/Notes";
import { PersonForm } from "./PersonForm";
import { getAll,create, replace} from "./Services/ApiCalls"; 


const App = () => {
  const [persons, setPerson] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [filterby, setNewFilter] = useState("");
  const [filtered, setFiltered] = useState([""]);
  const [updateList, setUpdateList] = useState(false);

  useEffect(() => {
    getAll()
    .then(data =>{
      setPerson([...data])      
    })
    .then(setUpdateList(false));
  },[updateList]);

  const handleOnChangeTitle = (event) => {
    setNewName(event.target.value);
  };
  const handleReset = () => {
    Array.from(document.querySelectorAll("input")).forEach(
      input => (input.value = "")
    );
  };
  const handleOnChangeBody = (event) => {
    setNewNumber(event.target.value);
  };
  const handleOnChangeFilter = (event) => {
    setNewFilter(event.target.value);
  };
  const handleFilter = (event) => {
    event.preventDefault();
    const filteredTitles = persons.filter((person) =>
      person.name.toLowerCase().includes(filterby.toLowerCase())
    );
    if (filteredTitles.length === 0) {
      alert("Person not found");
      setFiltered([]);
    } else {
      setFiltered([...filteredTitles]);
    }    
  };
  const handleSubmit = (event) => {
    event.preventDefault();   
    
    const personToAddToState = {
      name: newName,
      number: newNumber
    };
    if (newName === "" || newNumber === "") {
      alert("Fields can't be empty");
    } else if (persons.some(({ name }) => name === newName)) {
     //const personToReplace = persons.filter(pers => pers.name === newName)
     const personToReplace = persons.find(pers => pers.name === newName)
      console.log(personToReplace)
      if(window.confirm(`${personToReplace.name} is already added to phonebook, replace the old number with a new one?`)){
        const changedPerson = {...personToReplace, number: personToAddToState.number}
        console.log(changedPerson)
        replace(personToReplace.id, changedPerson).then(setUpdateList(true))
      }
      //alert(newName + " is already added to phonebook");
    } else {
      setPerson([...persons,personToAddToState]);
      console.log(personToAddToState);
      create(personToAddToState);
    }    
    handleReset();
  };
  return (
    <div>
      <h2>Phonebook</h2>
      <Filter
        handleFilter={handleFilter}
        handleOnChangeFilter={handleOnChangeFilter}
        filterby={filterby}
        filtered={filtered}
      />
      <h3> add a new</h3>
      <PersonForm
        handleSubmit={handleSubmit}
        handleOnChangeTitle={handleOnChangeTitle}
        handleOnChangeBody={handleOnChangeBody}
        newName={newName}
        newNumber={newNumber}
      />
      <h3>Numbers</h3>
      <Notes
        person={persons}
        setUpdateList={setUpdateList}
      />
    </div>
  );
};

export default App;