export const PersonForm = ({
    handleSubmit,
    handleOnChangeTitle,
    newTitle: newName,
    handleOnChangeBody,
    newBody: newNumber
  }) => {
    return (
      <form onSubmit={handleSubmit}>
        <div>
          Name:{" "}
          <input type="text" onChange={handleOnChangeTitle} value={newName} />
        </div>
        <div>
          Number:{" "}
          <input type="text" onChange={handleOnChangeBody} value={newNumber} />
        </div>
        <div>
          <button type="submit">add</button>
        </div>
      </form>
    );
  };