import { deleteName } from "../Services/ApiCalls";
export const Note = ({ person, setUpdateList }) => {  
  const deleteFromList = () =>{
    if(window.confirm(`Delete ${person.name}?`)){
      const id = person.id;
      const personToDelete = Object.values(person).filter(pers => pers.id !== id);
      console.log(personToDelete);    
      deleteName(id,personToDelete)
      .then(setUpdateList(true))}    
  }
    return (
      <li>
        <p>
          <strong>{person.name}</strong> {person.number} <button 
          onClick={deleteFromList}>Delete</button>
        </p>
      </li>
    );
  };